import React from "react";

import "./HeaderItem.css";

class HeaderItem extends React.Component {
    constructor(p) {
        super(p);
        this.state = { image: null };
        import("../assets/header/" + this.props.img).then(m => this.setState({ image: m.default }));
    }

    render() {
        return (
            <div className="header-item">
                <a href={this.props.link}><img src={this.state.image} alt={this.props.alt}></img></a>
            </div>
        )
    }
}

export default HeaderItem;