import React from "react";
import "./FeedItem.css";

class FeedItem extends React.Component {

    constructor(p) {
        super(p);
        this.state = {
            image: null
        }
        import("../assets/content/" + this.props.img).then(m => { this.setState({ image: m.default }) });
    }

    render() {
        let flexDirection = this.props.left ? "row" : "row-reverse";
        let textAlign = this.props.left ? "left" : "right";

        return (
            <div className="feed-item" style={{ flexDirection }}>
                <div className="feed-desc" style={{ textAlign }}>
                    <h1>{this.props.title}</h1>
                    <p>{this.props.desc}</p>
                    { this.props.links ? <div className="feed-btns">
                        {this.props.links.map(([title, link], i) => (<button key={i}><a href={link}>{title}</a></button>))}
                    </div> : ""}
                </div>
                <img src={this.state.image} alt="Zeno"></img>
            </div>
        )
    }
}

export default FeedItem;